<template>
    <div class="">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-message"></i> 消息中心</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container" v-loading="loading"> 
            <div class="form-top">
                <el-radio-group size="small" @change="changetab" v-model="choosed">
                  <el-radio-button label="未读消息"></el-radio-button>
                  <el-radio-button label="已读消息"></el-radio-button>
                </el-radio-group>
            </div>
            <el-table
              :data="items"
              size="small"
              style="width: 100%;margin-top:20px;">
              <el-table-column
                type="index"
                width="100px"
                label="序号">
              </el-table-column>
              <el-table-column
                prop="teachingName"
                label="课程设计">
              </el-table-column>
              <el-table-column
                prop="senderName"
                label="发送人">
              </el-table-column>
              <el-table-column
                label="消息内容">
                 <template  slot-scope="scope">
                    <div class="conten">{{scope.row.content}}</div>
                 </template>
              </el-table-column>
              <el-table-column
                prop="createtime"
                label="发送时间">
              </el-table-column>
              <el-table-column label="操作" width='150px'>
                <template  slot-scope="scope">
                    <el-button size="small" type="primary" @click="gowork(scope.row.workId)">进入作业</el-button>
                 </template>
              </el-table-column>
            </el-table>

            <div class="pagination">
              <el-pagination
                background
                :page-size="pageSize"
                @current-change="changePage"
                layout="prev, pager, next"
                :total="total">
              </el-pagination>
          </div>
        </div>
    </div>
</template>

<script>
import {fetchGetteachermessage} from '../../axios/requests'
    export default {
        name: 'tabs',
        data() {
            return {
                choosed:'未读消息',
                loading:false,
                pageNum:1,
                pageSize:10,
                total:0,
                items: []
            }
        },
        mounted(){
            this.Getteachermessage()
        },
        methods: {
            gowork(id){
                this.$openBlank('Sworkdetail',{id});
            },
            changetab(e){
                this.pageNum = 1;
                this.Getteachermessage();
            },
            changePage(page){
              this.pageNum = page;
              this.Getteachermessage();
            },
            async Getteachermessage() {//得到教学列表
              this.loading = true;
              let {pageNum, pageSize, choosed} = this;
              var status = choosed=="未读消息"?0:1;
              try {
                let response = await fetchGetteachermessage(status, pageNum, pageSize);
                if (response.status.code ==0) {
                  this.total = response.result.total;
                  this.items = response.result.list;
                }else{
                  this.$message.error(response.status.description);
                }
              } catch (e) {
                console.log(e);
              } finally {
                this.loading = false;
              }
            },
        }
    }

</script>

<style>
.message-title{
    cursor: pointer;
}
.handle-row{
    margin-top: 30px;
}
.conten{
    width:100%;
    text-overflow: -o-ellipsis-lastline;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
    }
</style>



